* {
    padding: 0;
    margin: 0;
    list-style: none;
    border: none;
    text-decoration: none;
    box-sizing: border-box;
    -webkit-overflow-scrolling: touch;
    font-family: "Mulish", sans-serif;

    &:focus {
        outline: 0;
    }
}
body {
    background: #F7F8FC;
    display: grid;
    place-items: center;
    height: 100vh;
}

.component {
    &--blog-cards {
        max-width: 1070px;
        ul {
            display: flex;
            margin: 0 -25px;

            li {
                padding: 0 25px;
                width: calc(100% / 3);
                display: flex;
                a {
                    display: flex;
                    flex-direction: column;
                    background: #fff;
                    border-radius: 10px;
                    overflow: hidden;
                    box-shadow: 0px 34px 36px -14px #e9ecf9;

                    > img {
                        width: 100%;
                    }

                    .inner {
                        padding: 25px 25px 30px;
                        display: flex;
                        flex-direction: column;
                        flex: 1;
                        .category {
                            padding: 2px 10px;
                            border-radius: 10px;
                            background: var(--bgcolor);
                            display: flex;
                            align-self: flex-start;
                            color: #fff;
                            font-size: 12px;
                            text-transform: uppercase;
                            margin-bottom: 9px;
                        }
                        .title {
                            font-size: 18px;
                            font-weight: bold;
                            color: #10182f;
                            line-height: 23px;
                            margin-bottom: 10px;
                        }
                        .description {
                            font-size: 16px;
                            color: #9e9ea0;
                            line-height: 20px;
                            margin-bottom: 40px;
                        }
                        .author {
                            display: flex;
                            align-items: center;
                            margin-top: auto;

                            img {
                                width: 45px;
                                height: 45px;
                                border-radius: 50%;
                                margin-right: 10px;
                            }
                            .author-inner {
                                display: flex;
                                flex-direction: column;
                                .author-name {
                                    font-size: 16px;
                                    font-weight: bold;
                                    color: #10182f;
                                    margin-bottom: 3px;
                                }
                                .date {
                                    font-size: 14px;
                                    color: #545d7a;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}
